import React from 'react';
import './Header.css';

export const Header = (props) => {
    const handleClick = () => {
        props.switchMode()
    }
    return (
        <header style={{ backgroundColor: props.style.elements }}>
            <h2>Where in the world?</h2>
            <p onClick={handleClick}><i class={`${props.style.icon} fa-moon`}></i>{props.style.header}</p>
        </header>
    )
}
