import React from 'react';
import './CountryList.css';
import { SearchBar } from '../SearchBar/SearchBar.jsx';
import { Country } from '../Country/Country.jsx';

export const CountryList = props => {
    return (
        <section className='CountryList'>
            <SearchBar style={props.style} searchOnLoad={props.searchOnLoad} searchOnName={props.searchOnName} searchOnRegion={props.searchOnRegion} />
            <section className='List-section' >
                {props.countries && props.countries.map((country, i) => <Country key={'country_' + i} style={props.style} country={country} />)}
            </section>
        </section>
    )
}
