import React from 'react';
import './App.css';
import { Header } from '../Header/Header.jsx';
import { MoreInfo } from '../MoreInfo/MoreInfo.jsx';
import { Home } from '../Home/Home.jsx';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

let darkmode = {
  header: 'Dark Mode',
  icon: 'fas',
  color: 'hsl(0, 0%, 100%)',
  input: 'white',
  background: 'hsl(207, 26%, 17%)',
  elements: 'hsl(209, 23%, 22%)'
}

let lightmode = {
  header: 'Light Mode',
  icon: 'far',
  color: 'hsl(200, 15%, 8%)',
  input: 'hsl(0, 0%, 52%)',
  background: 'hsl(0, 0%, 90%)',
  elements: 'hsl(0, 0%, 95%)'
}

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      mode: {
        dark: true
      },
      style: {
        header: darkmode.header,
        icon: darkmode.icon,
        text: darkmode.color,
        input: darkmode.input,
        background: darkmode.background,
        elements: darkmode.elements
      }
    };
    this.switchMode = this.switchMode.bind(this);
    this.switchStyle = this.switchStyle.bind(this);
  }

  switchMode() {
    this.setState({ mode: { dark: !this.state.mode.dark } })
  }

  switchStyle() {
    this.switchMode()
    this.state.mode.dark ? this.setState({
      style: {
        header: lightmode.header,
        icon: lightmode.icon,
        text: lightmode.color,
        input: lightmode.input,
        background: lightmode.background,
        elements: lightmode.elements
      }
    }) : this.setState({
      style: {
        header: darkmode.header,
        icon: darkmode.icon,
        text: darkmode.color,
        input: darkmode.input,
        background: darkmode.background,
        elements: darkmode.elements
      }
    })
  }

  render() {
    return (
      <div className='App' style={{ backgroundColor: this.state.style.background, color: this.state.style.text }}>
        <Header style={this.state.style} switchMode={this.switchStyle} />
        <Router>
          <Switch>
            <Route exact path='/' render={(props) => <Home {...props} style={this.state.style} />} />
            <Route exact path='/MoreInfo/:name/:code' render={(props) => <MoreInfo {...props} style={this.state.style} />} />
            <Route exact path='/MoreInfo/:code' render={(props) => <MoreInfo {...props} style={this.state.style} />} />

          </Switch>
        </Router>
      </div>
    )
  }
}

export default App;
