import React from 'react';
import './SearchBar.css';

export class SearchBar extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            dropdown: false,
            display: "none",
            name: '',
            region: ''
        };
        this.handleNameChange = this.handleNameChange.bind(this);
        this.handleSearch = this.handleSearch.bind(this);
        this.handleClick = this.handleClick.bind(this);
        this.handleChoice = this.handleChoice.bind(this);
        this.changeDropdownState = this.changeDropdownState.bind(this);
    }

    handleNameChange(e) {
        this.setState({ name: e.target.value });
        this.props.searchOnName(e.target.value);
        if (!e.target.value) {
            this.props.searchOnLoad();
        }
    }

    changeDropdownState() {
        if (this.state.dropdown === false) {
            this.setState({ display: "none" });
        } else {
            this.setState({ display: "block" });
        }
    }

    handleClick() {
        this.setState({ dropdown: !this.state.dropdown });
        this.changeDropdownState();
    };

    handleChoice(event) {
        alert(event.target.innerText);
        this.setState({ region: event.target.id });
        this.props.searchOnRegion(event.target.id);
        document.getElementById("text-box text").innerHTML = event.target.innerHTML;

    };

    handleSearch() {
        this.props.searchCountries(this.state.name, this.state.region);
    }

    render() {
        return (
            <div className="SearchBar" onKeyPress={(e) => { if (e.key === 'Enter') { this.handleSearch() } }}>
                <div className=" input" style={{ backgroundColor: this.props.style.elements, color: this.props.style.text }}>
                    <i className="fas fa-search"></i>
                    <input style={{ backgroundColor: this.props.style.elements, color: this.props.style.text }} value={this.state.name} onChange={this.handleNameChange} placeholder='Search for a country...' type="search" />
                </div>
                <div onClick={this.handleClick} className="select-container" id="container">
                    <div style={{ backgroundColor: this.props.style.elements, color: this.props.style.text }} className="text-box" id="text-box">
                        <p><span id="text-box text">Filter By Region</span><i className="fas fa-chevron-down"></i></p>
                    </div>
                    <ul style={{ display: this.state.display, backgroundColor: this.props.style.elements, color: this.props.style.text }} onClick={this.handleChoice} className="options-box" id="options-box">
                        <li id="americas">America</li>
                        <li id="africa">Africa</li>
                        <li id="asia">Asia</li>
                        <li id="europe">Europe</li>
                        <li id="oceania">Oceania</li>
                    </ul>
                </div>
            </div>
        )
    }
}