import React from 'react';
import { CountryList } from '../CountryList/CountryList.jsx';
import { RestCountries } from '../../util/RestCountries';

export class Home extends React.Component {
    _isMounted = false;
    constructor(props) {
        super(props);
        this.state = {
            countries: []
        };
        this.searchOnLoad = this.searchOnLoad.bind(this);
        this.searchOnName = this.searchOnName.bind(this);
        this.searchOnRegion = this.searchOnRegion.bind(this);
    }

    async searchOnLoad() {
        RestCountries.searchOnLoad().then(countries => this.setState({ countries: countries }))
    }
    async searchOnName(name) {
        RestCountries.searchOnName(name).then(countries => this.setState({ countries: countries }))
    }
    async searchOnRegion(region) {
        RestCountries.searchOnRegion(region).then(countries => this.setState({ countries: countries }))
    }
    componentDidMount() {
        this._isMounted = true;
        this.searchOnLoad();
    }
    componentWillUnmount() {
        this._isMounted = false;
    }
    render() {
        return (
            <div className='Home'>
                <CountryList style={this.props.style} searchOnLoad={this.searchOnLoad} searchOnName={this.searchOnName} searchOnRegion={this.searchOnRegion} countries={this.state.countries} />
            </div>
        )
    }
}