import React from 'react';
import './Country.css';
import { Link } from 'react-router-dom';

export const Country = props => {
    return (

        <Link style={{ textDecoration: 'none', margin: '3rem' }} to={'/MoreInfo/' + props.country.name + '/' + props.country.callingCode}>
            <div className='Country' style={{ backgroundColor: props.style.elements, color: props.style.text }}>
                <figure className='image'>
                    <img src={props.country.imgSrc} alt={props.country.name} />
                </figure>
                <div className="Country-text" >
                    <h2>{props.country.name}</h2>
                    <p><span>Population:</span>{props.country.population}</p>
                    <p><span>Region:</span>{props.country.region}</p>
                    <p><span>Capital:</span>{props.country.capital}</p>
                </div>
            </div>

        </Link>
    )
}


