import React from 'react';
import './MoreInfo.css';
import { RestCountries } from '../../util/RestCountries';
import { Link } from 'react-router-dom';

export class MoreInfo extends React.Component {
    _isMounted = false;
    constructor(props) {
        super(props);
        this.state = {
            country: {}
        };
        this.searchOnCode = this.searchOnCode.bind(this);
        // this.onClick = this.onClick.bind(this);
        // this.componentWillUpdate = this.componentWillUpdate.bind(this);
    }
    async searchOnCode() {
        await RestCountries.searchOnCode(this.props.match.params.code).then(country => { this.setState({ country: country }); console.log(this.state.country) })
    }
    componentDidMount() {
        this._isMounted = true;
        window.addEventListener('load', this.searchOnCode());
    }
    componentWillUnmount() {
        this._isMounted = false;
    }

    componentDidUpdate() {
        this.searchOnCode();
        // window.removeEventListener('load', this.searchOnCode())
    }

    render() {
        return (
            <section className="Main" style={{ backgroundColor: this.props.style.background }}>
                <Link to='/'><button style={{ backgroundColor: this.props.style.elements, color: this.props.style.text, padding: '1.5rem 3rem' }}><i class='fas fa-arrow-left' style={{ marginRight: '1rem' }}></i> Back</button></Link>
                <section className='MoreInfo'>
                    <figure className='left'>
                        <img src={this.state.country.flag} alt={this.state.country.name} />
                    </figure>
                    <section className='right'>
                        <h1>{this.state.country.name}</h1>
                        <div className='container'>
                            <div className='innerLeft'>
                                <p><span>Native Name:</span>{this.state.country.nativeName}</p>
                                <p><span>Population:</span>{this.state.country.population}</p>
                                <p><span>Region:</span>{this.state.country.region}</p>
                                <p><span>Sub Region:</span>{this.state.country.subregion}</p>
                                <p><span>Capital:</span>{this.state.country.capital}</p>
                            </div>
                            <div className='innerRight'>
                                <p><span>Top Level Domain:</span>{this.state.country.topLevelDomain}</p>
                                {this.state.country.currencies && <p><span>Currencies:</span>{this.state.country.currencies.map(currency => currency.name).join(', ')}</p>}
                                {this.state.country.languages && <p><span>Languages:</span>{this.state.country.languages.map(language => language.name).join(', ')}</p>}
                            </div>
                        </div>
                        {this.state.country.borders && <div className='borderCountries'>
                            <span>Border Countries:</span> {this.state.country.borders.map((border, i) => <Link to={'/MoreInfo/' + border} key={'border_' + i} ><button style={{ backgroundColor: this.props.style.elements, color: this.props.style.text }} onClick={this.searchOnCode}>{border}</button></Link>)}
                        </div>}
                    </section>
                </section>
            </section>
        )
    }
}